import React, { Component } from 'react';

class ExchangeTable extends Component {

    constructor(props) {
        super(props);

        this.renderTableItem = this.renderTableItem.bind(this);
        this.showModal = this.showModal.bind(this);
    }

    showModal() {
        this.props.showModal();
    }


    renderTableItem(datas, dataIndex) {
        const specificModalVisibleHandler = () => {
            this.props.specificModalVisibleHandler();
        };

        return (
            <tr className="exchange-table">
                <td className="number-line no">123</td>
                <td className="half-line order-num">
                    <span id="more_info" onClick={specificModalVisibleHandler}>20170101341293214</span>
                </td>
                <td className="number-line product-name">AD-313</td>
                <td className="half-line buyer"></td>
                <td className="half-line phone-number">01024487085</td>
                <td className="number-line exchange-date">20170707</td>
                <td className="half-line reason"></td>
                <td className="half-line detail-reason">  </td>
                <td className="number-line status"></td>
                {/* TODO:        */}
                <td className="half-line handle"><button className="btn-yellow" onClick={this.props.showModal}></button></td>
            </tr>
        )

    };


    render() {

        return (
            <table className="order-table ns-R">
                <thead>
                    <tr>
                        <th rowSpan={2}>No.</th>
                        <th colSpan={2}></th>
                        <th colSpan={3}></th>
                        <th colSpan={2}>,  </th>
                        <th rowSpan={2}></th>
                        <th rowSpan={2}></th>
                    </tr>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th> </th>
                    </tr>
                </thead>
                <tbody>
                    {/*     */}
                    {this.renderTableItem()}
                    {this.renderTableItem()}
                    {this.renderTableItem()}
                    {this.renderTableItem()}
                </tbody>
            </table>
        );
    }
}

export default ExchangeTable;


